import axios from 'axios';
import http from '../plugins/http';
import store from '../store';
import WebClientCofeci from './WebClientCofeci';
import CreciHelper from "../helpers/CreciHelper";

export default class WebClientI2br {
  constructor(cpf, idregional, silent = false) {
    this.cpf = cpf;
    this.idregional = idregional;
    this.http = silent ? axios.create(http.conselhoApi.defaults) : http.conselhoApi;
  }

  /**
   * Obtem os metadados da ficha cadastral do corretor.
   * @returns {Promise<any>}
   */
  async getFicha() {
    const formData = new FormData();
    formData.append('cpf', this.cpf);
    formData.append('idregional', this.idregional.toString());
    const { data: ficha } = await this.http.post('/corretor/ficha.php', formData);
    return ficha;
  }

  /**
   * Atualiza a data de validade do CARP/Certidão na base de dados do COFECI usando a ficha do ConselhoNET
   * @param ficha - Se nenhuma ficha for informada, uma nova será buscada.
   * @returns {Promise<void>}
   */
  async updateDataValidade(ficha = null) {
    ficha = ficha ? ficha : await this.getFicha();
    const webClientCofeci = new WebClientCofeci(store.getters.cpf, store.getters.regional, true);
    await webClientCofeci.validadeDocumento(ficha.dados_gerais.creci, ficha.certidao.apto_certidao, ficha.certidao.validade);
    store.commit('setDataValidade', ficha.certidao.validade);
  }

  /**
   * Obtem metadados sobre a regularidade do corretor.
   * @returns {Promise<any>}
   */
  async getCertidaoInfo() {
    const {data} = await this.http.get('https://www.creci' + CreciHelper.idParaUf(this.idregional) + '.conselho.net.br/api_validade_carp_icorretor.php?CPF_CNPJ=' + this.cpf);
    return data;
  }
}
