import axios from 'axios';
import ImageHelper from "../helpers/ImageHelper";
import WebClientCofeci from "./WebClientCofeci";

export default class WebClientCreciRN {
  cpf;
  constructor(cpf) {
    this.cpf = cpf;
  }

  static async getFichaCadastral(cpf) {
    const url = 'https://crecirn-api.incorpnet.com.br/api/corretors/consultaFichaCadastral';
    const params = {cpf};
    const {data: ficha} = await axios.get(url, {params});
    const webClientCofeci = new WebClientCofeci(cpf, 7, true);
    const fichaCofeci = await webClientCofeci.buscaPessoaFJ();
    if (fichaCofeci && fichaCofeci.FormacaoEspecifica) {
      //Obtém a formação específica, dado que o regional não possui, apenas o COFECI.
      if (fichaCofeci.FormacaoEspecifica.toLowerCase() === 'técnico em transações imobiliárias') ficha.formacao = {titulo: 5};
      //TODO - ADICIONAR AS OUTRAS FORMAÇÕES, TEM QUE VEM A STRING QUE O COFECI APRESENTA PARA O NOME DA FORMAÇÃO.
    }
    //Corrigindo diferenças da API
    ficha.titulo_eleitor = ficha.tituloEleitor;
    ficha.dados_gerais.creci = parseInt(ficha.dados_gerais.creci);
    ficha.pj = ficha.pj.map(pj => {
      if (pj.data_inscricao && pj.data_inscricao.length > 10) pj.data_inscricao = pj.data_inscricao.substr(0, 10);
      return pj;
    });
    ficha.contato.telefones.celular = Array.isArray(ficha.contato.telefones.celular) ? ficha.contato.telefones.celular.filter(t => !!t).map(t => t.replace(/^0+/, '')) : [];
    ficha.contato.telefones.comercial = Array.isArray(ficha.contato.telefones.comercial) ? ficha.contato.telefones.comercial.filter(t => !!t).map(t => t.replace(/^0+/, '')) : [];
    ficha.contato.telefones.residencial = Array.isArray(ficha.contato.telefones.residencial) ? ficha.contato.telefones.residencial.filter(t => !!t).map(t => t.replace(/^0+/, '')) : [];
    return ficha;
  }
  async getFichaCadastral() {
    return await WebClientCreciRN.getFichaCadastral(this.cpf);
  }

  /**
   * Atualiza os dados cadastrais
   * @param cpf string
   * @param ficha
   * @returns {Promise<void>}
   */
  static async setFichaCadastral(cpf, ficha) {
    const url = 'https://crecirn-api.incorpnet.com.br/api/corretors/AtualizaFichaCadastral';
    await axios.post(url, ficha);
  }
  async setFichaCadastral(ficha) {
    await WebClientCreciRN.setFichaCadastral(this.cpf, ficha);
  }

  static async getAssinatura(cpf, base64 = false) {
    const url = 'https://crecirn-api.incorpnet.com.br/api/corretors/obterAssinatura';
    const params = {cpf};
    const response = await axios.get(url, { params, responseType: 'blob', validateStatus: status => status===200 || status===404 || status===500 });
    if (response.status !== 200) return null;
    if (!base64) return response.data;
    return await ImageHelper.blobToDataURI_async(response.data);
  }
  async getAssinatura(base64 = false) {
    return await WebClientCreciRN.getAssinatura(this.cpf, base64);
  }

  static async getFoto(cpf, base64 = false) {
    const url = 'https://crecirn-api.incorpnet.com.br/api/corretors/obterFoto';
    const params = {cpf};
    const response = await axios.get(url, { params, responseType: 'blob', validateStatus: status => status===200 || status===404 || status===500 });
    if (response.status !== 200) return null;
    if (!base64) return response.data;
    return await ImageHelper.blobToDataURI_async(response.data);
  }
  async getFoto(base64 = false) {
    return await WebClientCreciRN.getFoto(this.cpf, base64);
  }

  static async setAssinatura(cpf, blob) {
    const url = 'https://crecirn-api.incorpnet.com.br/api/corretors/atualizarAssinatura?cpf=' + cpf;
    const formData = new FormData();
    formData.append('assinatura', blob);
    return await axios.post(url, formData);
  }
  async setAssinatura(blob) {
    await WebClientCreciRN.setAssinatura(this.cpf, blob);
  }

  static async setFoto(cpf, blob) {
    const url = 'https://crecirn-api.incorpnet.com.br/api/corretors/atualizarFoto?cpf=' + cpf;
    const formData = new FormData();
    formData.append('foto', blob);
    return await axios.post(url, formData);
  }
  async setFoto(blob) {
    await WebClientCreciRN.setFoto(this.cpf, blob);
  }
}
