import axios from 'axios';
import http from '../plugins/http';
import CreciHelper from '../helpers/CreciHelper';
import store from '../store';
import moment from 'moment';

export default class WebClientCofeci {
  regional;
  http;
  formData;

  /**
   * @param {string} cpf
   * @param {number} idregional
   * @param {boolean} silent
   */
  constructor(cpf, idregional, silent = false) {
    this.regional = idregional;
    this.http = silent ? axios.create(http.cofeciApi.defaults) : http.cofeciApi;
    const formData = new FormData();
    formData.append("cpf", cpf);
    formData.append("token", CreciHelper.getCofeciToken(idregional));
    if (store.state.homologacao) formData.append('homologacao', 'S');
    this.formData = formData;
  }

  /**
   * Obtem os dados do corretor
   * @returns {Promise<*|null>} NULL caso não encontre-o na base de dados.
   */
  async buscaPessoaFJ() {
    const response = await this.http.post('/buscapessoafj.php', this.formData);
    const dadosFiltrados = response.data.find(r => r.IdCreci === this.regional);
    return dadosFiltrados ? dadosFiltrados : null;
  }

  /**
   * Obtem os dados do corretor.
   * @returns {Promise<*|null>} NULL caso ele não esteja apto a ver o cartão.
   */
  async buscaCorretor() {
    const response = await this.http.post('/buscacorretor.php', this.formData);
    const dadosFiltrados = response.data.find(r => r.IdCreci === this.regional);
    return dadosFiltrados ? dadosFiltrados : null;
  }

  /**
   * Obtem os dados do corretor.
   * @returns {Promise<*|null>} NULL caso ele não tenha registros  de recadastramento (pela API GravarRecadastramento).
   * @private
   */
  async _situacaoFinanceiraRecadastramento() {
    const response = await this.http.post('/situacaofinanceirarecadastramento.php', this.formData);
    const dadosFiltrados = response.data.find(r => r.IdCreci === this.regional);
    return dadosFiltrados ? dadosFiltrados : null;
  }

  /**
   * Realizou ou não o recadastramento.
   * @returns {Promise<boolean>}
   */
  async realizouRecadastramento() {
    const response = await this._situacaoFinanceiraRecadastramento();
    return !!response;
  }

  /**
   * Busca Email Corretor
   * @returns {Promise<Array<string>|boolean>} False = CPF não consta no banco do COFECI.
   */
  async buscaEmailCorretor() {
    const {data: dados} = await this.http.post('/buscaemailcorretor.php', this.formData);
    if (dados.length === 1 && parseInt(dados[0].Erro) === 2) return false;
    return dados.filter(x => x.IdCreci === this.regional).map(y => y.EmailPagina);
  }

  /**
   * Atualiza a validade do CARP
   * @param {number} numCreci - Creci do corretor
   * @param {boolean} emDia - Se ele está em dia
   * @param {string|null} dataValidade - Informe data somente se a aptidão for temporária (aaaa-mm-dd)
   * @returns {Promise<void>}
   */
  async validadeDocumento(numCreci, emDia, dataValidade = null) {
    const anoAtual = moment().year();
    const dataValidadePadrao = moment().diff(anoAtual+'-04-15', 'days') >= 0 ? anoAtual+'-04-15' : (anoAtual + 1)+'-04-15';
    await this.http.post('/validadedocumento.php', {
      "Token": CreciHelper.getCofeciToken(this.regional),
      "CodigoInscricao": numCreci,
      "IdCategoriaInscricao": 1,
      "Pessoa": "F",
      "IdSituacao": emDia ? (dataValidade ? 2 : 1) : undefined,
      "DataValidade": emDia ? (dataValidade ? dataValidade : dataValidadePadrao) : undefined,
      "DebitoCreci": emDia ? (dataValidade ? 'S' : 'N') : 'S',
    });
  }
}
