import axios from 'axios';
import httpInstance from '../plugins/http';

export default class WebClientCreciMS {
  http = httpInstance.axios;
  baseUrl = 'http://sis.crecims.gov.br/novo/api/services/';

  constructor(silent = false) {
    if (silent) this.http = axios;
  }

  /**
   * Obtem a listagem de pedidos de CARP do cidadão.
   * @param {string} cpf
   * @returns {Promise<any[]>}
   */
  async getCarpsFromCpf(cpf) {
    const { data } = await this.http.get(this.baseUrl + 'carp/index.php?cpf=' + cpf);
    return data;
  }

  /**
   * Registra um CARP.
   * @param {string} cpf CPF do corretor.
   * @param {number} creci Numero de inscricao no CRECI.
   * @param {string} nome Nome completo.
   * @param {number} exercicio Ano de exercicio.
   * @param {0|1|2|3} localEntrega Local da entrega/retirada.
   * @param {boolean} notificar Notifica por email sobre a criação do pedido.
   * @param {number} fonte Identifica o portal e a razão da inserção.
   * @returns {Promise<void>}
   */
  async insertCarp(cpf, creci, nome, exercicio, localEntrega, notificar = true, fonte = 3) {
    const formData = new FormData();
    formData.append('cpf', cpf);
    formData.append('creci', creci.toString());
    formData.append('nome', nome);
    formData.append('exercicio', exercicio.toString());
    formData.append('entrega', localEntrega.toString());
    formData.append('fonte', fonte.toString());
    formData.append('token', '64f1b358905f69f756025b6876ec3032');
    if (!notificar) formData.append('naonotificar', '1');
    await this.http.post(this.baseUrl + 'carp/index.php', formData);
  }
}