<template>
  <div class="carp-view">
    <!-- Loading Screen -->
    <div v-if="loading" class="text-center">
      <div class="spinner-border text-primary" role="status">
        <span class="sr-only">Carregando...</span>
      </div>
      <p v-if="typeof loading === 'string'" class="mb-0 mt-2">{{loading}}</p>
    </div>
    <!-- Feedback de erros -->
    <div v-else-if="erro" class="d-flex flex-column align-items-center justify-content-center flex-fill">
      <article class="text-center">
        <i class="fas fa-exclamation-triangle fa-4x text-danger"/>
        <p style="font-weight: 600" class="px-1 my-3">{{erro}}</p>
        <router-link v-if="podeRecadastrar && showRecadShortcut" class="btn btn-primary" :to="{name: 'recad-form-dadospessoais'}">Realizar recadastramento</router-link>
        <router-link class="btn btn-secondary mt-2" :to="{name: 'home'}">SAIR</router-link>
      </article>
    </div>
    <!-- CARP -->
    <div v-else>
      <transition name="flip" mode="out-in">
        <!-- Frente -->
        <img
          v-if="visualizacao === 0"
          :src="srcFrente"
          alt=""
          class="carp-face"
          :key="0"
        >
        <!-- Verso -->
        <img
          v-else-if="visualizacao === 1"
          :src="srcVerso"
          alt=""
          class="carp-face"
          :key="1">
      </transition>
      <v-speed-dial
        fixed
        bottom
        right
        :direction="'top'"
      >
        <template v-slot:activator>
          <v-btn
            color="#007bff"
            dark
            fab
            small
          >
            <v-icon>mdi-gesture-tap</v-icon>
          </v-btn>
        </template>
        <v-btn
          fab
          dark
          small
          :to="{name: 'home'}"
          color="#ff5252"
        >
          <v-icon>mdi-close</v-icon>
          <div class="fab-text-custom green">Sair</div>
        </v-btn>
        <v-btn
          fab
          dark
          small
          @click="virarCartao"
          color="#ffb400"
        >
          <v-icon>mdi-axis-z-rotate-counterclockwise</v-icon>
          <div class="fab-text-custom green">{{visualizacao === 0 ? 'Girar verso' : 'Girar frente'}}</div>
        </v-btn>
      </v-speed-dial>
    </div>
  </div>
</template>

<script>
import DateHelper from "../helpers/DateHelper";
import CreciHelper from "../helpers/CreciHelper";
import ImageHelper from "../helpers/ImageHelper";
import {WebClientI2br, WebClientSpw, WebClientCreciPE, WebClientCreciRN} from "../http";
export default {
  name: "Carp",
  data: () => ({
    erro: 0, //0 = sem erro, 1 = não está regular (genérico), 2 = erro de carregamento de dados, 3 = irregular com recadastramento, 4 = em débito,
    showRecadShortcut: false,
    visualizacao: 0, // 0 = frente, 1 = verso
    loading: true,
    srcFrente: '',
    srcVerso: '',
    zoomLevel: 1,
  }),
  methods: {
    async validarAptidao() {
      // Verifica se o usuário esta apto, atribuindo o valor adequado a variavel "erro"
      this.loading = 'Validando aptidão';
      await this.sleep(1000);

      //==== Validações de aptidão ====//
      //Está ativo?
      if (CreciHelper.utilizaConselhoNet(this.$store.getters.regional)) {
        if (this.$store.state.situacao !== 1) this.erro = 'Você não está ativo no conselho';
      }
      //Fez o recadastramento?
      if (!this.erro && !this.$store.state.recadastrado) this.erro = 'Você está irregular com o recadastramento';
      //Está em débito?
      if (!this.erro && CreciHelper.utilizaConselhoNet(this.$store.getters.regional) && !this.$store.state.anuidade) {
        this.erro = 'Você está em débito com o regional';
      }
      //Tratativas para Não-clientes I2BR
      if (!this.erro && !CreciHelper.utilizaConselhoNet(this.$store.getters.regional)) {
        //CRECI-RJ e CRECI-MG
        if (this.$store.getters.regional === 1 || this.$store.getters.regional === 4) {
          const clientHttpSpw = new WebClientSpw(this.$store.getters.regional);
          const financeiroSpw = await clientHttpSpw.getSituacaoCadastralFinanceira(this.$store.getters.cpf);
          if (financeiroSpw) {
            if (financeiroSpw.situacaoCadastral !== "ATIVO") this.erro = 'Você não está ativo no conselho';
            else if (financeiroSpw.situacaoFinanceira !== "EM DIA") this.erro = 'Você não está em dia com o conselho regional';
            else if (financeiroSpw.dataValidade) {
              //dataVencimentoProximaParcela é uma string em formato de "dd/mm/aaaa", senão é uma string vazia.
              this.validadeDia = parseInt(financeiroSpw.dataValidade.substr(0,2));
              this.validadeMes = parseInt(financeiroSpw.dataValidade.substr(3,2));
              this.validadeAno = parseInt(financeiroSpw.dataValidade.substr(6,4));
            }

            //Atualiza o Cofeci (Assincrono)
            clientHttpSpw.updateSituacaoCadastralFinanceira(financeiroSpw, this.$store.state.creci, CreciHelper.getCofeciToken(this.$store.getters.regional), false, this.$store.state.homologacao)
          }
        }
      }
      //==== Fim das validações ====//

      if (!this.erro) {
        this.loading = 'Obtendo data de validade';
        //Atualiza o COFECI sobre a data de validade só pra garantir que gerem um cartão com a data correta
        if (CreciHelper.utilizaConselhoNet(this.$store.getters.regional)) {
          const webClientI2br = new WebClientI2br(this.$store.getters.cpf, this.$store.getters.regional, true);
          await webClientI2br.updateDataValidade();
        } else if (this.$store.getters.regional === 7 || this.$store.getters.regional === 17) {
          //CRECI-PE
          const webClientI2br = new WebClientI2br(this.$store.getters.cpf, this.$store.getters.regional, true);
          await webClientI2br.updateDataValidade(this.$store.getters.regional === 17 ?
            await WebClientCreciRN.getFichaCadastral(this.$store.getters.cpf) :
            await WebClientCreciPE.getFichaCadastral(this.$store.getters.cpf)
          );
        }

        this.loading = 'Montando cartão';
        try {
          const {data: frente} = await this.$http.appApi.get('/carp/frente.php', {
            responseType: 'blob',
            params: {
              token: CreciHelper.getCofeciToken(this.$store.getters.regional),
              cpf: this.$store.getters.cpf
            }
          });
          const {data: verso} = await this.$http.appApi.get('/carp/verso.php', {
            responseType: 'blob',
            params: {
              token: CreciHelper.getCofeciToken(this.$store.getters.regional),
              cpf: this.$store.getters.cpf
            }
          });
          this.srcFrente = await ImageHelper.blobToDataURI_async(frente);
          this.srcVerso = await ImageHelper.blobToDataURI_async(verso);
        } catch (e) {
          if (e && e.response && e.response.headers && e.response.headers['content-type'] && e.response.headers['content-type'].includes('application/json')) {
            const log = await this.blobToJson(e.response.data);
            if (log.mensagem) this.erro = log.mensagem;
          }
        }
      }
      this.$store.commit('setControls', false);
      this.loading = false;
    },
    virarCartao() {
      if (this.visualizacao === 0) this.visualizacao++;
      else this.visualizacao--;
    },
    zoomIn() {
      if (this.zoomLevel < 3) this.zoomLevel += .1;
    },
    zoomOut() {
      if (this.zoomLevel < 3) this.zoomLevel -= .1;
    },
    sleep(ms) {
      return new Promise(resolve => setTimeout(resolve, ms));
    },
    blobToJson(blob) {
      return new Promise(resolve => {
        const fr = new FileReader();
        fr.onload = () => {
          resolve(JSON.parse(fr.result));
        };
        fr.readAsText(blob);
      });
    },
  },
  created() {
    this.validarAptidao();
  },
  destroyed() {
    this.$store.commit('setControls', true);
  },
  computed: {
    regional() {
      return "CRECI "+this.$store.getters.regional+"ª Região - " + CreciHelper.idParaUf(this.$store.getters.regional, true);
    },
    datanascimento() {
      return DateHelper.date_SQLparaBR(this.$store.state.datanascimento)
    },
    datainscricao() {
      return DateHelper.date_SQLparaBR(this.$store.state.datainscricao)
    },
    formacao() {
      switch (this.$store.state.formacao) {
        case 2:
          return 'TÉCNICO EM TRANSAÇÕES IMOBILIÁRIAS';
        case 3:
          return 'GESTOR IMOBILIÁRIO';
        default:
          return 'CORRETOR DE IMÓVEIS (Lei nº 6.530/1978)';
      }
    },
    podeRecadastrar() {
      return CreciHelper.utilizaConselhoNet(this.$store.getters.regional);
    },
  },
}
</script>

<style scoped>
  .carp-view {
    display: flex;
    flex: 1 1 auto; /* preenche toda a altura a disposição */
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .carp-face {
    width: 100%;
    max-width: 100%;
    max-height: 100vh;
    object-fit: contain;
  }
  .fab-text-custom {
    position: absolute;
    right: 50px;
    background-color: rgba(0,0,0,0.5);
    padding: 10px;
    box-shadow: 0px 3px 5px -1px rgba(0, 0, 0, 0.2), 0px 6px 10px 0px rgba(0, 0, 0, 0.14), 0px 1px 18px 0px rgba(0, 0, 0, 0.12);
    border-radius: 2px;
  }
</style>
